import React, {Component} from 'react'
import PropTypes from 'prop-types'
import Link from 'next/link'
import Title from '../components/styles/Title';
import styled from 'styled-components';

const ItemStyled = styled.div `
 background: ${props => props.theme.darkGrey};
  border: .7px solid ${props => props.theme.lightGrey};
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-flow:colum;
  border-radius:18px;
  background-color: white;
  width:400px;
  height: 300px;
`;

const CandAllInfo = styled.div `
display: grid;
grid-template-columns: repeat(2, 1fr);
height:250px;
`;

const ButtonList = styled.div `
display:grid;
grid-template-columns:repeat(2, 1fr);
    width: 100%;
    border-top: 3px solid ${props => props.theme.lightGrey};
    /* grid-template-columns: repeat(auto-fit, minmax(50px, 1fr)); */
    grid-gap: 3px;
    background: ${props => props.theme.lightGrey};
    height:60px;
    & > * {
      background: #357;
      border: 0;
      font-family: 'radnika_next';
      font-size: 1.5rem;
      padding: .9rem;
    }
  
`;

const CandImg = styled.div `
   width:200px;
   height:200px; 
   img {
    max-width: 100%;
    object-fit: cover;
    border-radius: 12px;
    
  }

  `;

const RestInfo =styled.div`

display: flex;
flex-direction:column;
align-items:left;

`;
 const UpdateBtn =styled.div`
    background-color:#359;
 `;
  const DelBtn = styled.button`
  background-color:#371;
  `;

class Candidate extends Component {
    static propTypes = {
        eachCandidate: PropTypes.object.isRequired
    }

    render() {
        const {
            image,
            id,
            candCode,
            cand1stName,
            cand2ndName,
            cand3rdName,
            email,
            phoneNumb,
            placeOfBirth
        } = this.props.eachCandidate
        return (

            <ItemStyled>
                <CandAllInfo>
                    <CandImg>

                        {image && <img src={image} alt={cand1stName}/>}
                    </CandImg>
                    <RestInfo>
                        <div className="itemInfo">
                            <p>First Name: {cand1stName}</p>
                        </div>
                        <div className="itemInfo">
                            <p>2nd Name: {cand2ndName}</p>
                        </div>
                        <div className="itemInfo">
                            <p>Last Name: {cand3rdName}</p>
                        </div>
                        <div className="itemInfo">
                            <p>Code: {candCode}</p>
                        </div>
                        <div className="itemInfo">
                            <p>Email: {email}</p>
                        </div>
                        <div className="itemInfo">
                            <p>Birth Place: {placeOfBirth}</p>
                        <div className="itemInfo">
                            <p>Phone: {phoneNumb}</p>
                        </div>
                        </div>
                    </RestInfo>
                </CandAllInfo>
                <ButtonList>
                    <UpdateBtn>
                            <Link href={{
                                pathname: "/update",
                                query: {id: id} }}>
                                <a>   Update </a>
                            </Link>
                    </UpdateBtn>
                    <DelBtn>Delete</DelBtn>
                </ButtonList>
            </ItemStyled>
        )
    }
}
export default Candidate;